console.log("hello");

import { data } from "./data.js";

const table = document.querySelector(".table");

const addClientsMarkup = () => {
  const markup = [];
  console.log(markup);
  data.map(({ customerName, company, phoneNumber, email, country, status }) =>
    markup.push(`<tr>
    <th>${customerName}</th>
    <th>${company}</th>
    <th>${phoneNumber}</th>
    <th>${email}</th>
    <th>${country}</th>
    <th>${status}</th>
    </tr>`)
  );
  table.innerHTML = markup.join("");
};
addClientsMarkup();
